.dark-mode{
    background-color: darken($grisOscuro, 10%);
    p {
        color: $blanco;
    }
    a,h2,h1,h3,h4 {
        color: $gris;
    }

    .contenedor-anuncio {
        background-color: $grisOscuro;
        border: 1px solid $grisOscuro;
        .precio {
            color: $verde;
        }
    }
    .formulario {
        color: $blanco;
        input {
            background-color: $gris;
        }
        textarea {
            background-color: $gris;
        }
        select {
            background-color: $gris;
        }
    }
    input.boton-verde{
        &:hover {
            background-color: darken($naranja, 10%);
        }
    }
}

